@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap");
* {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  width: 80%;
  margin: 0px;
  margin-left: 10%;
}

header {
  margin-top: 20px;
}
header .header__top {
  width: inherit;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px dashed var(--grey);
}
header .header__top .sections {
  width: auto;
  gap: 8px;
  display: flex;
  align-items: center;
}
header .header__top .sections__button {
  width: 30px;
  height: 30px;
  padding: 6px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
header .header__top .sections__button span {
  width: 100%;
  height: 0.4px;
  border: 1px solid var(--grey);
}
header .header__top .sections h2 {
  font-family: "Roboto";
  font-weight: 500;
  margin: 0;
}
header .header__top .center-h2 {
  font-family: "PT Serif";
  font-weight: 400;
  font-size: 20px;
  opacity: 0.5;
}
header .header__top .right-part {
  display: flex;
  align-items: center;
  gap: 20px;
}
header .header__top .right-part .search {
  display: inline-flex;
  align-items: center;
  gap: 5px;
}
header .header__top .right-part .search img {
  width: 13px;
  height: 13px;
}
header .header__top .right-part .search h2 {
  font-family: "Roboto";
  font-weight: 500;
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}
header .header__top .right-part .login {
  width: 75px;
  height: 30px;
  border: none;
  cursor: pointer;
}
header .header__top .right-part .login h2 {
  font-family: "Roboto";
  font-weight: 400;
  margin: 0;
  font-size: 14px;
  line-height: 15px;
}
header h1 {
  font-family: "Bitter";
  font-weight: 700;
  font-size: 64px;
  letter-spacing: -4px;
  text-align: center;
  margin: 10px 0;
  margin-bottom: 25px;
}
header .topics {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--grey);
  border-top: 1px solid var(--grey);
  margin-bottom: 40px;
}
header .topics__invisible-div {
  width: 76px;
}
header .topics ul {
  display: inline-flex;
  gap: 15px;
  justify-content: space-between;
}
header .topics ul li {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 20px;
}
header .topics a {
  display: flex;
  align-items: center;
  gap: 5px;
  font-family: "Roboto";
  font-weight: 700;
  color: var(--link-color);
  font-size: 13px;
}
header .topics a img {
  width: 20px;
  height: 16px;
}

main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main .main-content {
  width: 100%;
}
main .main-content img {
  width: inherit;
  z-index: 0;
}
main .main-content__title {
  position: relative;
  width: 77%;
  height: auto;
  background-color: #fff;
  padding: 10px;
  z-index: 9999999;
  margin-top: -70px;
  margin-left: 20px;
}
main .main-content__title h2 {
  width: 100%;
  height: auto;
  font-family: "PT Serif";
  font-weight: 700;
  font-size: 27px;
  margin: 0;
}
main .main-content p {
  font-family: "Roboto";
  font-weight: 400;
  margin-left: 20px;
  font-size: 15px;
}

:root {
  --grey: #d8d8d8;
  --link-color: #0C79FF;
}